html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** Media Queries **/
.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.center-container {
  max-width: 80rem;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.not-webp .start {
  background-image: url(../img/header.jpg);
}

.webp .start {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.start {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.start {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.start .header-content {
    min-height: 70rem;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .footer-container {
  padding: 2.5rem;
}
.footer .navegation {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-button {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-button:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-button {
    margin-top: 0;
  }
}

.bar {
  display: flex;
  align-items: center;
  padding-top: 3rem;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegation {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 8.5s linear, height linear;
  height: 0;
}
@media (min-width: 768px) {
  .navegation {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegation a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegation a {
    margin-right: 2rem;
  }
  .navegation a:last-of-type {
    margin-right: 0;
  }
}

/***mobile-menu*/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .us-icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .advertisements-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advertisement {
  background-color: #f5f5f5;
  border: 1px solid #ebebeb;
  margin-bottom: 3rem;
}
.advertisement .advertisement-content {
  padding: 2rem;
}
.advertisement h3, .advertisement p {
  margin: 0;
}

.price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.characteristics-icons {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.characteristics-icons li {
  flex: 1;
  display: flex;
}
.characteristics-icons li img {
  flex: 0 0 5rem;
  margin-right: 1rem;
}
.characteristics-icons li p {
  font-weight: 900;
}

.btn-bg-yellow {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-bg-yellow:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-bg-yellow-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.btn-bg-yellow-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-bg-green {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-bg-green:hover {
  background-color: #619800;
  cursor: pointer;
}

.btn-bg-red-block {
  background-color: #f85151;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.btn-bg-red-block:hover {
  background-color: #f73838;
  cursor: pointer;
}

.btn-bg-green-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.btn-bg-green-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.not-webp .contact-img {
  background-image: url(../img/encuentra.jpg);
}

.webp .contact-img {
  background-image: url(../img/encuentra.webp);
}

.contact-img {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-img h2 {
  font-weight: #000000;
  font-size: 4rem;
  color: #FFFFFF;
}
.contact-img p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .lower-seccion {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog-input {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog-input {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog-input:last-of-type {
  margin-bottom: 0;
}

.input-text a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .input-text a {
    margin-top: 0;
  }
}
.input-text h4 {
  margin: 0;
  line-height: 1.4;
}
.input-text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.description-meta span {
  color: #E08709;
}

.testimonial {
  background: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 8rem;
  height: 6rem;
  display: block;
  position: absolute;
  left: -2rem;
  top: 0;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form .contact-form {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

.dark-mode {
  background-color: #1f1d1d;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .advertisement, .dark-mode .summary-property {
  background-color: #333333;
  border: 1px #333333;
}
.dark-mode .advertisement .price, .dark-mode .summary-property .price {
  color: #71B100;
}
.dark-mode .iconInvert {
  filter: invert(100%);
}
.dark-mode .us-text blockquote {
  color: #FFFFFF;
}
.dark-mode .form label,
.dark-mode .form legend,
.dark-mode .form p {
  color: #e1e1e1;
}
.dark-mode .form input:not([type=submit]),
.dark-mode .form textarea,
.dark-mode .form select {
  background-color: #333333;
  color: #FFFFFF;
  border: solid 1px #e1e1e1;
}

@media (min-width: 768px) {
  .us-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.us-text blockquote {
  font-weight: #000000;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem;
}

.alert {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alert.error {
  background-color: #f85151;
}
.alert.success {
  background-color: #71B100;
}
.alert.warning {
  background-color: #fdf7a5;
}

table.properties {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.properties thead {
  background-color: #71B100;
}
table.properties thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.properties .table-picture {
  width: 10rem;
}

.small-pictures {
  width: 10rem;
}