@use 'base/variables' as var;
@use 'base/mixins' as m;

.form{
    p{
        font-size: 1.4rem;
        color:var.$grisOscuro ;
        margin: 2rem 0 0;
    }

    legend{
        font-size: 2rem;
        color:var.$grisOscuro ;
    }

    label{

        font-weight: var.$bold;
        text-transform: uppercase;
        display: block;
    }

    input:not([type="submit"]) ,
    textarea,
    select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: var.$blanco;
        border: 1px solid var.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
   
    .contact-form{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    input[type="radio"]{
        margin: 0;
        width: auto;
    } 

}